@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Karla:wght@300;400;500;600;700;800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Source+Code+Pro&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Anton&display=swap');
@import "react-slideshow-image/dist/styles.css";

:root{
 --bgcolor:#212f45;
 --bgcolor2:#212b3d;
 --cardcolor:#0b525b;
 --link:#fff;
 --social_button:#9ef01a;
 --footer:#41444a;
}
.themed{
    --bgcolor:#ffffff;
    --bgcolor2:#d2d2d2;
    --cardcolor:#8800ff;
    --link:rgb(0, 0, 0);
    --social_button:#ff8205;
   }

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    
}

html {
    scroll-behavior: smooth;
    max-width: 100%;
   
}

body {
    background: var(--bgcolor);
    color: var(--link);
    
}

.bodypart {
    background: var(--bgcolor);
    max-width: 100%;
    
    
}


/* ===========================================Navbar==================================================================================== */

.Navbar {
    padding: 10px;
    width: 95%;
    background-color:var(--bgcolor);
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    position: relative;
}
.themebutton{
    display: flex;
    
}
.themebutton .light{
    display: block;
}
.themebutton .dark{
    display: none;
}
.active .light{
    display: none;
}
.active .dark{
    display: block;
}

.tg{
    position:absolute;
}
.Navbar .Logo {
    margin: 0% 2%;
    width: 3rem;
    height: 3rem;
}

.Navbar .Logo img {
    width: 3rem;
    height: 3rem;
}

.Serchbox {
    border-radius: 30px;
    display: flex;
    padding: 0% 1%;
    Margin: 0% 1%;
    flex-wrap: wrap;
    width: 400px;
    overflow: hidden;
    height: 2.5rem;
    color: rgb(255, 255, 255);
    background-color: rgba(53, 52, 52, 0.644);
    box-shadow: 0px 0px 7px rgba(0, 0, 0, 0.397) inset;
}

.Serchbox input,
.Serchbox input::placeholder {
    background-color: rgba(53, 52, 52, 0);
    border: none;
    padding: 1% 3%;
    outline: none;
    color: rgba(255, 255, 255, 0.452);
    width: 340px;
    height: 2.5rem;
    font-weight: bold;
    font-family: 'Courier New', Courier, monospace;
}

.links {
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 1%;
    width: 30rem;
}

.links a {
    text-decoration: none;
    color: var(--link);
    Margin: 0% 1%;
    font-weight: bold;
    font-family: 'Source Code Pro', monospace;
    cursor: pointer;
    transition: .6s;
}

.links a:hover {
    color: var(--social_button);
}


/* ----------------------------------Landing Page-----============================================================================= */

.Landingpage {
    width: 100%;
    height: 100vh;
    background:var(--bgcolor);
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    overflow: hidden;
    padding: 2%;
}

.infoPart {
    display: flex;
    justify-content: center;
    box-sizing: border-box;
    width: 50%;
    height: 100%;
    overflow: hidden;
    align-items: center;
    flex-direction: column;
}

.projectPart {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.contentpart::-webkit-scrollbar {
    display: none;
}

.contentpart {
    background: var(--bgcolor2);
    box-shadow: 0 0 5px 0px rgba(0, 0, 0, 0.26) inset;
    background-position: center;
    border-radius: 15px;
    overflow: scroll;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 50%;
    height: 100%;
    align-items: center;
    transition: .7s;
}

.contentpart h1 {
    width: 100%;
    padding: 1%;
    color: var(--link);
    font-family: 'Poppins', sans-serif;
    text-align: center;
    box-shadow: 0px 0px 15px rgba(61, 60, 60, 0.13);
}

.contentpart:hover {
    width: 80%;
}

.infoPart .infoPartimage {
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 2px 2px 0px rgb(255, 255, 255);
    width: 300px;
    height: 300px;
    border-radius: 50%;
    box-sizing: border-box;
    overflow: hidden;
}

.infoPart .infoPartimage .infoPartimage_bg {
    padding: 2%;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 98%;
    height: 98%;
    border-radius: 50%;
    background-image: url("./resources/my.png");
    transition: .6s;
    background-size: cover;
}

.infoPart .infoPartimage_bg:hover {
    transform: translateY(-5px);
}

.Detailspart {
    width: 69%;
    margin: 25px;
    font-family: 'Poppins', sans-serif;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: var(--social_button)
}

.Detailspart h2 {
    font-size: 1.2em;
    height: 50px;
    font-weight: 500;
    letter-spacing: 2px;
    font-family: 'Source Code Pro', monospace;
    color: var(--social_button);
}

.Detailspart:hover {
    transform: translateX(0);
    opacity: 1;
}

.socialmedia {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 40px 0px;
    padding: 5%;
    font-size: 2em;
}

.socialmedia div {
    padding: 5% 30%;
    transition: .6s;
}

.socialmedia div a {
    color: var(--link);
}

.socialmedia div:hover,
.socialmedia div:hover a {
    color: var(--social_button);
    transform: scale(1.5);
}

button {
    padding: 10px 20px;
    background-color: var(--bgcolor2);
    border-radius: 15px;
    outline: none;
    border: none;
    color: oldlace;
    font-size: 14px;
    font-family: 'Source Code Pro', monospace;
    margin: 5% 0% 0% 0%;
    box-shadow: 0 0 5px 0px rgba(0, 0, 0, 0.26) inset;
    cursor: pointer;
}

button:hover {
    background-color: var(--social_button);
    color: rgb(0, 0, 0);
}


/* ==============================================================Card=================================================================== */

.Maincard {
    margin: 5%;
}

.Card {
    width: 300px;
    max-height: 450px;
    overflow: hidden;
    border-radius: 25px;
    background-color: var(--cardcolor);
    text-align: center;
    transition: .7s;
}

.Card:hover {
    transform: translateY(-15px);
}

.image {
    width: 300px;
    max-height: 300px;
    overflow: hidden;
    
}
.image h2{
    text-align: center;
  transform: translateY(80px);
  color: var(--link);
  transition: .5s ease-in-out;
}
.Card img {
    filter: blur(2px) opacity(.2);
    width: 300px;
    transform: scale(1.2);
    transition: .7s;
}

.Card img:hover{
    filter: opacity(1) blur(0px);
    transform: scale(1.4);
}
.Card .image:hover h2{
    transform: translateY(-400px);
}

.details {
    width: 90%;
    overflow: hidden;
    text-align: left;
    padding: 20px;
}

.details h2 {
    margin: 2% 0%;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    text-transform: uppercase;
    color: rgb(247, 247, 247);
}

.details p {
    font-size: 14px;
    font-family: 'Source Code Pro', monospace;
    color: rgb(185, 185, 185)
}


/* ====================================Form================================================================================================= */
.HeadingForm{
    text-align: center;
    color: var(--link);
    margin-top: 20PX;
}
.Form {
    max-width: 90%;
    height: 100vh;
    display: flex;
    justify-content: center;
    box-sizing: border-box;
    overflow: hidden;
    align-items: center;
    
    margin: 5%;
    margin-top: 20px;
    padding: 40px;
    background: var(--bgcolor2);
}

.Form .input{
    width: 90%;
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
    text-align: center;
}
.Form .Greeting {
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    min-width: 300px;
    max-width: 70%;
    overflow: hidden;
}
.input h1 {
    text-align: center;
    padding: 2%;
    width: 90%;
    font-size: 20px;
    color: rgb(255, 255, 255);
    font-weight: 500;
}

.Form h2 {
    text-align: center;
    font-size: 20px;
    color: rgb(255, 255, 255);
    font-weight: 500;
}

.Form .inputbox {
    width: 400px;
    margin-top: 10px;
}

.Form .inputbox input,
.Form .inputbox textarea {
    width: 300px;
    padding: 5px 0;
    font-size: 16px;
    color: var(--link);
    margin: 10px 0;
    border: none;
    background: var(--cardcolor);
    border-bottom: 2px solid var(--bgcolor2);
    outline: none;
    resize: none;
}

.input .inputbox span {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 500;
    display: flex;
    justify-content: center;
    text-align: left;
    align-items: center;
    color: var(--link);
}
.pgd h1{
    margin: 2%;
    text-align: center;
    color:var(--link);
}
.projectgalery{
    width: 100%;
    overflow-x: scroll;
    display: flex;
}
.projectgalery::-webkit-scrollbar {
    display: none;
}
.Footer{
   max-width: 100%;
   overflow: hidden;
    
    padding: 20px;
    box-sizing: border-box;
    background-color: var(--footer);
    display: flex;
   
}
.Footer .leftsidef{
    width: 60%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}
.leftsidef .Logo{
    
    display: flex;
    justify-content: center;
    align-items: center;
    width: 300px;
    height: 300px;
}
.leftsidef .Logo img{
    width: 100%;
}
.Footer .rightsidef{
    width: 70%;
    
}
.Footer .rightsidef h1{
    text-align: center;
    font-size: 2.4em;
    padding: 20px;
    color: var(--link);
}
.Contact-info{
    padding: 20px;
    font-size: 1.2em;
    text-align: left;
    width: 100%;
    color: var(--link);
}
.Contact-info span{
    padding: 10px 2px;
    display: flex;
  
    align-items: center;
}
.Contact-info span h2{
    padding: 0 20px;
}
.Contact-follow{
    font-size: 2em;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.Contact-follow a{
    color :var(--link); 
    padding: 0 20px;
    text-decoration: none;
   
}
.Contact-follow a:hover{
    color: var(--social_button);
   
}
.Whatsapp{
    display: flex;
    padding: 9px;
    font-size: .4em;
    border: 2px solid var(--footer);
    
    background-color: var(--social_button);
    border-radius: 25px;
    text-align: center;
    transition: .2s ease-in-out;
}
.Whatsapp:hover{
    border: 2px solid var(--cardcolor);
    background-color: var(--link);
}
/* ==========================================Media querryp==============================================================================*/

@media only screen and (max-width: 1024px) {
    .themebutton{
       position: absolute;
       top: 0;
      right: 0;
      margin-right: 20px;
      margin-top: 10px;
        
    }
    .Detailspart h2 {
        font-size: 1.3em;
        text-align: center;
        height: 50px;
        margin: 2%;
    }
    .Navbar {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .Serchbox {
        margin: 4% 2%;
        padding: 0% 2%;
    }
    .Card {
        width: 270px;
    }
    .Landingpage {
        height: 200vh;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 4% 2%;
    }
    .contentpart {
        width: 90%;
        height: 70%;
    }
    .contentpart:hover {
        width: 100%;
    }
    .projectPart {
        display: flex;
        overflow-y: hidden;
        flex-direction: column;
        width: 100%;
        height: 88%;
    }
    .infoPart {
        width: 100%;
    }
    .projectPart::-webkit-scrollbar {
        display: none;
    }
}

.fix{
    z-index: 2222222222;
   
    position: fixed;
    top:90%;
    
    left:90%;
 
    color: var(--link);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.fix a{
    background-color: var(--cardcolor);
    width: 50px;
    height: 50px;
    padding:16px 10px;
    text-decoration: none;
    text-align: center;
    border-radius: 50%;
    font-weight: 900;
    font-size: 2em;
    line-height: 8px;
    color: rgba(255, 255, 255, 0.618);
    animation-name: animate ;
    animation-duration: 3s;
    font-stretch: 20px;
    animation-iteration-count: infinite;
    box-shadow: 0 0 20px 6px;
    
}
.fix a span{
    font-size: .5em;
}

@keyframes animate {
  0% {transform: translateY(-8px);}
    50% {transform: translateY(16px);}
    100% {transform: translateY(-8px);}
  }

/* ========================================Part 2========================================================================================= */

