        font-size: 20px;
        color: rgb(255, 255, 255);
        font-weight: 500;
    }
    .Form .inputbox {
        width: 90%;
        margin-top: 10px;
    }
    .Form .inputbox input,
    .Form .inputbox textarea {
        width: 200px;
    }
    .input .inputbox span {
        font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
        font-weight: 400;
        display: flex;
        justify-content: left;
        align-items: center;
        color: var(--link);
    }
    .infoPart .infoPartimage {
        width: 200px;
        height: 200px;
        border-radius: 50%;
        box-sizing: border-box;
        overflow: hidden;
    }
    
